.hero__item{
    border-radius: 20px;
}
.hero__search__categories{
    padding:0px !important;
}
*:focus{
    outline:0px !important;
}
.hero__search__categories select{
    width:calc(100% - 20px);
    height: -webkit-fill-available;
    border-width:0px;
    padding:10px 10px;
    font-weight:bolder;
}

.breadcrumb li.active{
    font-weight: bolder;
    color: #343a40 !important;
}

.breadcrumb li a{
    color: #555555 !important;
}

.breadcrumb li:hover a{
    color: #343a40 !important;
}

.admin_profile{
    background-color: rgb(255, 255, 255);
    border-radius: 2px;
}

.custom-higligted-row{
    color: black;
    border-radius: 3px;
    border-width: 0px;
    background-color: rgb(253, 253, 253);
    box-shadow: 10px 10px 30px rgb(175, 175, 175)
}
.switch-wrapper{
    
}

.my-loader{
    width: 100%;
    height: calc(100% + 100vh);
    background: #383d41;
    opacity: 0.78;
    display: none;
    position: fixed;
    z-index: 1009000000;
}

.my-loader img {
    width: 80px;
    height: 80px;
    margin-top: calc(50vh - 40px);
    margin-left: calc(50vw - 40px);
}
